        p Segn <em>Kotler y Keller</em> (2012), el mercadeo intenta lograr la identificacin y satisfaccin de las necesidades del ser humano de forma rentable. La empresa <em>eBay</em>, al reconocer que sus clientes no podan ubicar unos artculos deseados por ellos, se dio a la tarea de crear un foro para atenderlos de manera online. La empresa <em>IKEA</em>, al darse cuenta de que sus clientes deseaban buenos muebles con precios ms bajos, los redujo. Las dos empresas entendieron el mercadeo y la inteligencia de <em>marketing</em>, convirtiendo una necesidad individual o social en una oportunidad empresarial que dio como resultado su rentabilidad.
