    p.mb-5 El mercadeo, para definirlo desde el contexto social, muestra una funcin basada en la sociedad; por ejemplo, el rol del mercadeo es la entrega de un modelo de vida de mejor calidad. Por eso, es un proceso social en el que existen grupos de personas e individuos que logran lo que necesitan y desean a travs de la creacin, comercio e intercambio de productos de valor para ellos. Los especialistas de mercadeo promocionan 10 tipos principales de artculos, a saber: 
