    p Por otro lado, tambin estn los que comercializan, o sea, los especialistas en mercadeo, que en realidad son especialistas en bsqueda de respuestas de un cliente potencial, como, <b>por ejemplo, llamar la atencin, comprar, votar, donar, suscribirse. </b>
    p.mb-5 Son estrategas que poseen la capacidad de estimular la compra de los productos, pero esa es una visin limitada de lo que hacen. Por eso, los especialistas en mercadeo se responsabilizan de administrar la demanda, buscando la influencia en el nivel, de coordinar tiempo y componer la demanda para dar cumplimiento a las metas pactadas por la empresa. Los siguientes son los ocho estados de demanda.
