    .titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 1.1 <em>Marketing</em> en entornos digitales 
    .row.mb-5.justify-content-center.align-items-center(data-aos="fade-right")
      .col-lg-4.mb-4.mb-lg-0
        figure
          img(src='@/assets/curso/tema1/img24.png').m-auto
      .col-lg-8(data-aos="fade-left")
        p Segn Xie y Min (2019), el objetivo del <em>marketing</em> digital es aprovechar internet y dems formas de promocin <em>online</em> para comunicarse con el pblico objetivo de forma rentable, y permitiendo un trabajo colaborativo con socios empresariales con los que pueda existir un inters en comn. Cuando se habla de <em>marketing</em> digital, primero se habla de estrategias y herramientas alojadas en la <em>web</em>, y segundo, se habla tambin de las pantallas digitales a travs de computadores de escritorio, porttiles, celulares, <em>tablets</em>, televisores, consolas de videojuegos y relojes <em>smart</em>. 
        p.mb-5 Es uno de los pilares en los que, por ejemplo, las agencias de turismo deben apoyarse si desean conquistar una parte de las ventas realizadas por una comunidad de turistas 2.0 que estn en bsqueda de un viaje a travs de internet. En estos entornos digitales se puede buscar informacin sobre los destinos deseados y tener una asesora en cuanto a los precios. Esta informacin y asesora es presentada por agencias de viajes <em>online u OTA (On Line Travel Agency).</em>
    .row.mb-5.justify-content-center
      .col-12.col-md-8
        .bg3.px-5.py-4.position-relative
          p.mb-3.ps-4.text-white Algo muy importante que tiene la <em>web</em> es que los clientes pueden encontrar una gran cantidad de opciones, desde sitios de destino hasta precios, que pasan desde los costosos hasta los econmicos. Por eso, la importancia de promocionarse en entornos digitales.
          img.img-a3.d-none.d-lg-block(src='@/assets/curso/tema1/img16.svg')
          img.img-a4(src='@/assets/curso/tema1/img17.svg')
    .row.mb-3(data-aos="fade-right")
