              img(src='@/assets/curso/tema1/img25.svg') 
          .col-auto.me-2
            h3.mb-0 Las 4F del <em>marketing</em> digital
        
    p.mb-5 El <em>marketing</em> digital se basa en 4F, que vienen siendo variables compuestas por una estrategia de mercadeo efectiva, como lo hace el <em>marketing mix</em> y sus 4P: <b>precio, producto, plaza e impulsin</b>. En el caso de las 4F se incluyen <b>flujo, funcionalidad, <em>feedback</em> y fidelizacin</b> las cuales se detallan a continuacin. 

    TabsB.color-acento-contenido.mb-5(data-aos="fade") 
      .py-4.py-md-5(titulo="Flujo" :icono="require('@/assets/curso/tema1/img26.svg')")
        .row.justify-content-center.align-items-center
          .col-lg-8.mb-4.mb-lg-0
            h4 Flujo
            p.mb-0  Tiene que ver con la multiplataforma o transversalidad. El cliente debe sentirse atrado por la interactividad generada en el sitio <em>web</em> de tal manera que le llame y se interese por la marca y sus productos. Actualmente, los sitios <em>web</em> buscan ser lo ms precisos posibles para exponer y ordenar los botones de bsqueda (por lo general en la pgina de Inicio) para que los clientes encuentren fcilmente lo que desean (un producto, un servicio, una ciudad destino, etc.).      
          .col-lg-3.col-6
            figure
              img(src="@/assets/curso/tema1/img30.svg", alt="alt")

      .py-4.py-md-5(titulo="Funcionalidad" :icono="require('@/assets/curso/tema1/img27.svg')")
        .row.justify-content-center.align-items-center
          .col-lg-8.mb-4.mb-lg-0
            h4 Funcionalidad
            p La navegacin en el sitio <em>web</em> tiene que ser fcil e intuitiva, de lo contrario, el cliente termina abandonndolo.
          .col-lg-3.col-6
            figure
              img(src="@/assets/curso/tema1/img31.svg", alt="alt")

      .py-4.py-md-5(titulo="<em>Feedback</em>" :icono="require('@/assets/curso/tema1/img28.svg')")
        .row.justify-content-center.align-items-center
          .col-lg-8.mb-4.mb-lg-0
            h4 <em>Feedback</em>            
            p.mb-0 La interaccin con los clientes es vital para construir una excelente relacin con ellos. La reputacin lograda es clave para conseguir confianza y una comunicacin bidireccional. Se recomienda ser humildes, humanos, transparentes y sinceros. Para lograr el <em>feedback</em> se puede dar oportunidad a los clientes de dejar comentarios, iniciar un chat online de asesora, o aceptar una suscripcin para recibir el envo de boletines u ofertas por <em>e-mail</em>.     
          .col-lg-3.col-6
            figure
              img(src="@/assets/curso/tema1/img32.svg", alt="alt")

      .py-4.py-md-5(titulo="Fidelizacin" :icono="require('@/assets/curso/tema1/img29.svg')")
        .row.justify-content-center.align-items-center
          .col-lg-8.mb-4.mb-lg-0
            h4 Fidelizacin            
            p.mb-0 Luego de entablar una relacin con los clientes, se deben fidelizar, la idea es que tengan compromiso por la marca y cautivarlos ofreciendo contenidos de inters. Esta variable es de vital importancia para el futuro empresarial. Hoy, el foco es el cliente, y por eso la estrategia digital debe contener espacios selectos en donde l interacte, optimizando los resultados en motores de bsqueda, influenciando opiniones e incluso <em>influencers</em>, y estudiando la informacin que los diferentes canales digitales suministren para mejorar el rendimiento de las acciones puestas en marcha. 

          .col-lg-3.col-6
            figure
              img(src="@/assets/curso/tema1/img33.svg", alt="alt")
