            h3.mb-0 El plan de <em>marketing</em> 
   
    p.mb-5 Para <em>Kotler y Keller</em> (2012), el plan de <em>marketing</em> es una herramienta de gestin empresarial que puede garantizar a las compaas la rentabilidad, siempre y cuando su desarrollo y ejecucin sea eficiente. Los estrategas de mercadeo deben tener en cuenta que los objetivos comerciales deben ser coherentes con los objetivos generales de la marca. Por eso, en el desarrollo del plan de <em>marketing</em> se deben elegir muy bien las estrategias y el plan de accin que encamine a la empresa a lograr los objetivos pactados. Algunos de los beneficios que suministra un plan de mercadeo son:   
      
    .row.mb-5
      .col-12.col-lg-4.mb-3.h-100(data-aos="zoom-in")
        .row.bg5
          .col-3.px-0.align-items-center
