          .col-9.d-flex.align-items-center.p-4
            p.mb-0 <b>Fortalecer la toma de decisiones comerciales y de mercadeo</b>. 
      .col-12.col-lg-4.mb-3.h-100(data-aos="zoom-in")
        .row.bg5
          .col-3.px-0.align-items-center
            figure
              img.transf(src='@/assets/curso/tema1/img35.svg', alt='Texto que describa la imagen')
          .col-9.d-flex.align-items-center
            p.mb-0 <b>Forjar un programa de acciones para la actividad comercial</b>. 
      .col-12.col-lg-4.mb-3.h-100(data-aos="zoom-in")
        .row.bg5
          .col-3.px-0.align-items-center
            figure
              img.transf(src='@/assets/curso/tema1/img36.svg', alt='Texto que describa la imagen')
          .col-9.d-flex.align-items-center
            p.mb-0 <b>Facilitar la realizacin de las acciones comerciales y de mercadeo</b>. 
    .row.mb-5.justify-content-center
      .col-12.col-md-8
        .bg3.px-5.py-4.position-relative
          p.mb-3.ps-3.text-white Con base en las ventajas que suministran, las marcas que deseen optimizar su eficacia y competencia deben delinear y tramitar #[b planes de mercadeo que brinden pilares como el anlisis, la planeacin, la gestin y la revisin de las acciones comerciales, de forma clara y precisa]. Si estos cuatro pilares son desarrollados estratgicamente, las marcas pueden hallar la solucin a muchos de sus inconvenientes de gestin y obtener la obtencin de sus objetivos de rentabilidad (<em>Kotler y Keller</em>, 2012).
          img.img-a5.d-none.d-lg-block(src='@/assets/curso/tema1/img37.svg')
          img.img-a6(src='@/assets/curso/tema1/img17.svg')
    .row.mb-5.justify-content-center.align-items-center
      .col-lg-8(data-aos="fade-right")
        p <em>Rivelli</em> (2019) afirma que la elaboracin de un plan de mercadeo es el resultado de un minucioso proceso en el que cada fase va a estar directamente conectada con las dems, con el fin de la obtencin final del documento. El liderazgo en la preparacin de un plan de mercadeo depende de cada marca y de su organizacin interna. Por lo general, las <em>Pymes</em> se centran en la figura de los responsables de mercadeo o del mismo gerente, si la compaa es pequea.
        p En el mercado se pueden encontrar herramientas de <em>software</em> para desarrollar planes de mercadeo. Estas herramientas se disean de forma especial para <em>Pymes</em> y pequeos empresarios, que, a corto plazo y de una forma sencilla, podrn aprender a elaborar y revisar sus propios planes de mercadeo efectiva y rentablemente.
      .col-lg-4.mb-4.mb-lg-0(data-aos="fade-left")
        figure
          img(src='@/assets/curso/tema1/img38.png').m-auto
    .row.mb-3(data-aos="fade-right")
