        .row.align-items-center
          .col-auto.pe-0
            figure
              img(src='@/assets/curso/tema1/img25.svg') 
          .col-9.col-lg-auto.me-3
            h3.mb-0 Etapas del plan de <em>marketing online</em> 
              
    p Una vez establecido el plan de mercadeo tradicional, se ponen en marcha actividades que se pueden desarrollar desde internet. Inicialmente, tener presencia en la <em>web</em> es relativamente fcil, analizado desde lo tcnico y lo econmico. En segunda instancia, ser efectivo con la estrategia de mercadeo digital es ms complejo, puesto que el simple hecho de crear una <em>web</em> no es suficiente para lograr visibilidad; se requiere de planear una estrategia, con objetivos delimitados de manera perfecta y con herramientas y vas para hacerlos realidad. 
    p.mb-5 Se desarrollan a continuacin las etapas del plan de mercadeo digital.

    .row.align-items-center.mb-3(data-aos="zoom-in")
      .col-auto.mb-2
        figure
          img(src="@/assets/curso/tema1/img39.svg", alt="alt")
      .col
        h4.mb-0.bgtex Etapa 1.        
        p.mb-0.tamp Anlisis y diagnstico de la situacin
        .col-md-4.borde
       
    p.mb-5 Anlisis juicioso de los entornos interno y externo de la empresa, que se debe materializar en el DOFA, que es un estudio indefectible en el diagnstico de la situacin empresarial. En esta etapa se lleva a cabo una aguda investigacin en las siguientes tres reas: 
    .tarjeta--azul1.p-5.mb-5
      SlyderA(tipo="b")(data-aos="fade-right")
        .row.align-items-center.justify-content-center
          .col-lg-8.mb-4.mb-lg-0.ps-3
            h4 rea 1 - Empresa
