        p Las <em>Pymes</em> deben tener en cuenta que, una vez realizado el trabajo anterior, deben contar con recursos econmicos con los que pueda llevar a cabo el plan de mercadeo digital. El modelo econmico y financiero de este presupuesto debe contener datos importantes como son: 
