    #t_1_2
    .titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 1.2 Planeacin estratgica 
    .row.mb-5.justify-content-center.align-items-center(data-aos="fade-right")
      .col-lg-4.mb-4.mb-lg-0
        figure
          img(src='@/assets/curso/tema1/img77.png').m-auto
      .col-lg-8(data-aos="fade-left")
        p <em>Kotler y Keller</em> (2012) expresan que las estrategias y los planes creativos son los ingredientes clave del proceso de direccin de mercadeo y, adems, son lo que pueden guiar las actividades de <em>marketing</em> de la empresa. El desarrollo correcto de la estrategia demanda tener disciplina y flexibilidad, por lo se recomienda que las marcas se adhieran a una estrategia, y mejorarla constantemente.
        .row.bg5.p-3.mb-3
          .col-lg-10
            p.bg5 Uno de los objetivos de las marcas es <b>la entrega de valor para el pblico objetivo con un beneficio</b>. En una economa tan competitiva, con pblicos cada vez ms informados y con muchas opciones, la marca solo puede ganar ajustando el proceso de entrega de valor y elegir, suministrar e informar un valor preferente. 
          .col-lg-2
            figure
             img.img-t(src='@/assets/curso/tema1/img76.svg').mx-auto
        p La visin tradicional del mercadeo es que la marca produce algo para venderlo; el mercadeo ocurre exactamente en el proceso de venta. Las marcas que tienen esta visin poseen ms oportunidades de xito en economas que se caracterizan por la escasez de productos, y donde los clientes no exigen por calidad, caractersticas o estilo. Pero, esta visin tradicional no funciona en economas donde encuentra distintos tipos de clientes, con deseos, percepciones, predilecciones y juicios de compra personales. La marca que compite de forma inteligente disea y entrega ofertas para mercados objetivo bien especficos.
    p.mb-5 A continuacin, se expone la descripcin de cada una de las estrategias a implementar en este proceso, plan de mercadeo, planeacin estratgica y planeacin tctica. 
    .row.justify-content-center.mb-5
      .col-md-6.col-lg.mb-5.mb-lg-0
        .tarjeta-avatar
          img(src='@/assets/curso/tema1/img78.svg', style="width: 102px")
          .tarjeta.color-acento-contenido1
            .p-4
              p <b>El plan de mercadeo</b> es la herramienta principal para administrar y coordinar el esfuerzo de <em>marketing</em>; <b>el cual opera en dos niveles: estratgico y tctico</b>. 
      .col-md-6.col-lg.mb-5.mb-lg-0
        .tarjeta-avatar
          img(src='@/assets/curso/tema1/img79.svg', style="width: 102px")
          .tarjeta.color-acento-contenido1
            .p-4
              p <b>La planeacin estratgica</b> de mercadeo establece los mercados objetivo y la propuesta de valor de la marca de acuerdo al estudio de las mejores oportunidades de mercado. 
      .col-md-6.col-lg.mb-5.mb-lg-0
        .tarjeta-avatar
          img(src='@/assets/curso/tema1/img80.svg', style="width: 102px")
          .tarjeta.color-acento-contenido1
            .p-4
              p <b>La planeacin tctica</b> de mercadeo especifica las tcticas de <em>marketing</em> que contienen las caractersticas del producto, publicidad, comercio, adjudicacin de precios, distribucin y servicio. 
    p.mb-5 Como complemento al desarrollo de una adecuada y bien formulada planeacin estratgica veamos en la siguiente imagen la secuencia correspondiente a la planificacin, implementacin y control. 
    .row.align-items-center.justify-content-center.mb-5(data-aos="fade-right")
      .col-lg-12
        .titulo-sexto.color-acento-contenido.mb-5
          p.mb-0 #[b Figura 1]. Grafica planeacin estratgica                    
        figure
          img(src='@/assets/curso/tema1/img81.svg')
    p.mb-5 La consolidacin de una planeacin estratgica implica integrar a toda la organizacin y hacer nfasis en unos niveles puntuales como lo son la misin corporativa, establecer unidades estratgicas de negocios, asignar recursos a cada unidad estratgica de negocios y evaluar las oportunidades de crecimiento. Estos elementos se describen de forma detallada a continuacin.
    .row.mb-3(data-aos="fade-right")
      .col-auto.bg4.p-0
        .row.align-items-center
          .col-auto.pe-0
            figure
              img(src='@/assets/curso/tema1/img25.svg') 
          .col-auto.me-3
            h3.mb-0 Definir la misin corporativa
    .row.mb-0.mb-lg-5.justify-content-center.align-items-center(data-aos="fade-right")
      .col-lg-2.mb-4.mb-lg-0
        figure
          img.img-t(src='@/assets/curso/tema1/img82.svg').m-auto
      .col-lg-10(data-aos="fade-left")
        p Una marca vive para alcanzar algo: fabricar autos, prestar servicios, construir casas, etc. La misin empresarial puede ir cambiando con el paso del tiempo, de tal forma que se puedan aprovechar nuevas oportunidades o para dar respuesta a nuevos contextos del mercado. Se deben hacer las siguientes preguntas a la hora de definir la misin empresarial: 
    .row.mb-0.mb-lg-5.justify-content-center
      .col-12.col-md-12
        .px-5.py-4.position-relative
          figure
            img.transf.img84(src="@/assets/curso/tema1/img84.svg", alt="alt")
            img.img-a7.transf(src='@/assets/curso/tema1/img83.svg')
    .row.mb-5.justify-content-center(data-aos="fade-right")
      .col-lg-2.mb-4.mb-lg-0
        figure
          img.img-t(src='@/assets/curso/tema1/img85.svg').m-auto
      .col-lg-10(data-aos="fade-left")
        p Parecen fciles de responder, pero, en realidad, son las ms complicadas que la marca responder en su historia. 
        p Poseer una misin clara e inteligente suministra un sentido cooperado de propsito, direccin y oportunidad, por lo que debe ser compartida con los empleados e incluso clientes. Una buena misin debe conllevar a una visin que proporcione una direccin empresarial para los siguientes, 5, 10 o 20 aos. Sony, por ejemplo, quera ofrecer un equipo de sonido personal y porttil, creando as el walkman y el reproductor de CD porttil.
        p.mb-5 Las mejores declaraciones de misin tienen cinco caractersticas principales.
    .row.mb-5.justify-content-center
      .col-12.col-lg-4.mb-3.h-100(data-aos="zoom-in")
        .row.bg5
          .col-3.px-0.align-items-center
            figure
              img.transf(src='@/assets/curso/tema1/img140.svg', alt='Texto que describa la imagen')
          .col-9.d-flex.align-items-center
            div
              p.mb-0 Centrarse en un nmero limitado de metas. No exigir condiciones que no se puedan cumplir. 
      .col-12.col-lg-4.mb-3.h-100(data-aos="zoom-in")
        .row.bg5
          .col-3.px-0.align-items-center
            figure
              img.transf(src='@/assets/curso/tema1/img141.svg', alt='Texto que describa la imagen')
          .col-9.d-flex.align-items-center
            div
              p.mb-0 Destacar valores y polticas esenciales de la empresa. 
      .col-12.col-lg-4.mb-3.h-100(data-aos="zoom-in")
        .row.bg5
          .col-3.px-0.align-items-center
            figure
              img.transf(src='@/assets/curso/tema1/img142.svg', alt='Texto que describa la imagen')
          .col-9.d-flex.align-items-center
            div
              p.mb-0 Definir las ms importantes esferas competitivas dentro de las que maniobrar la marca. 
      .col-12.col-lg-4.mb-3.h-100(data-aos="zoom-in")
        .row.bg5
          .col-3.px-0.align-items-center
            figure
              img.transf(src='@/assets/curso/tema1/img143.svg', alt='Texto que describa la imagen')
          .col-9.d-flex.align-items-center
            div
              p.mb-0 Tener una visin de largo plazo. La misin cambia solo cuando deje de ser importante.
      .col-12.col-lg-6.mb-3.h-100(data-aos="zoom-in")
        .row.bg5
          .col-2.px-0.d-flex.align-items-center
            figure
              img.transf(src='@/assets/curso/tema1/img144.svg', alt='Texto que describa la imagen')
          .col-10.d-flex.align-items-center
            div
              p.mb-0 Ser corta, memorable y significativa. Ms que una declaracin de misin, en la actualidad, se recomienda redactar enunciados corporativos con pocas palabras; Por ejemplo, American Express expresa Proveer la mejor experiencia del usuario cada da.      
    .row.mb-3(data-aos="fade-right")
      .col-auto.bg4.p-0
        .row.align-items-center
          .col-auto.pe-0
            figure
              img(src='@/assets/curso/tema1/img25.svg') 
          .col-9.col-lg-auto.me-3
            h3.mb-0 Establecer Unidades Estratgicas de Negocios (UEN)

    .row.mb-5.justify-content-center.align-items-center(data-aos="fade-right")
      .col-lg-4.mb-4.mb-lg-0
        figure
          img.img-t(src='@/assets/curso/tema1/img86.svg').m-auto
      .col-lg-8(data-aos="fade-left")
        p Las empresas hacen parte de un tipo de negocio, negocio de autos o negocio textil. Por eso el negocio se define como un proceso de satisfaccin del cliente. Los productos pueden ser fugaces, pero, las necesidades bsicas y los grupos de clientes son eternos. 
        p Transportarse es una necesidad y por eso el caballo, el auto, el taxi, el bus, el camin, el tren, el barco y el avin son productos que satisfacen esa necesidad. 
        p Las unidades son divisiones de la empresa que se pueden gestionar de forma individual, con su propia misin y visin, diferentes a las de la marca general. O sea, que una compaa con un grupo diverso de negocios tiene varias unidades estratgicas apartadas, con productos que se dirigen a cierto mercado con una ubicacin geogrfica especfica.
    .bg7
      .row.mb-5.justify-content-center.align-items-center(data-aos="fade-right")
        .col-lg-10(data-aos="fade-left")
          p Existen tres dimensiones en las que un negocio se puede definir: <b>grupos de clientes, necesidades de los clientes y tecnologa</b>. Una microempresa que establece su negocio como creacin de sistemas de iluminacin incandescente para estudios televisivos, tendr como clientes los estudios de televisin. La necesidad de estos estudios es la iluminacin, y la tecnologa es que esta sea incandescente. La pequea empresa, si lo desea, se puede expandir para crear iluminacin de viviendas, empresas y locales, o tambin puede ofrecer a los mismos estudios de televisin, calefaccin, aire acondicionado o ventilacin. 
        .col-lg-2.mb-4.mb-lg-0
          figure
            img.img-t(src='@/assets/curso/tema1/img87.svg').mx-auto
    p.mb-5 Para complementar el concepto, en la siguiente figura se consolidan de forma puntual, cada uno de los pasos que se deben gestionar dentro de la estructuracin de la planeacin estratgica en una unidad de negocio. 
    .row.align-items-center.justify-content-center.mb-5(data-aos="fade-right")
      .col-lg-12
        .titulo-sexto.color-acento-contenido.mb-5
          p.mb-0 #[b Figura 2]. Grafica planeacin estratgica de las unidades de negocio.
      .col-lg-10                 
        figure
          img(src='@/assets/curso/tema1/img88.svg')
          figcaption <b>Nota</b>. Tomada de  <em>Facebook</em> Agencia <em>Monkey Plus </em> - la planificacin estratgica en unidades de negocios
    .row.mb-3(data-aos="fade-right")
      .col-auto.bg4.p-0
        .row.align-items-center
          .col-auto.pe-0
            figure
              img(src='@/assets/curso/tema1/img25.svg') 
          .col-9.col-lg-auto.me-3
            h3.mb-0 Asignar recursos a cada unidad estratgica de negocios
    .row.mb-5.justify-content-center.align-items-center(data-aos="fade-right")
      .col-lg-4.mb-4.mb-lg-0
        figure
          img.img-t(src='@/assets/curso/tema1/img89.png').m-auto
      .col-lg-8(data-aos="fade-left")
        p Algunos modelos de planeacin de carteras suministran los medios para tomar decisiones de inversin. Existen mtodos nuevos basados en analizar el valor para el accionista, y en si el valor de mercado de una marca es mejor con o sin una unidad de negocio (incluso si esta se vende o se retira). 
        p Estos clculos valoran la fortaleza de un negocio con base en sus opciones de crecimiento, teniendo como medidas su capacidad de expansin mundial, de reposicionamiento o redireccin, y de subcontratacin estratgica.
    .row.mb-3(data-aos="fade-right")
      .col-auto.bg4.p-0
        .row.align-items-center
          .col-auto.pe-0
            figure
              img(src='@/assets/curso/tema1/img25.svg') 
          .col-8.col-lg-auto.me-3
            h3.mb-0 Evaluar las oportunidades de crecimiento
    p.mb-5 Esto incluye tanto la planeacin de nuevos negocios, como el retiro de negocios antiguos. El proceso de planificacin estratgica de las unidades de negocio consta de los siguientes pasos (<em>Kotler y Keller</em>, 2012):
    .bg8
      .row.mb-5.justify-content-center.align-items-center(data-aos="fade-right")
        .col-lg-5(data-aos="fade-left")
          h4 Paso 1. La misin del negocio.     
          p Cada unidad de negocio define su misin especfica dentro del contexto empresarial, para que sea coherente con sus recursos y contexto. 
          h4 Paso 2. Anlisis DOFA.      
          p Evaluacin general de las fortalezas, oportunidades, debilidades y amenazas, que es una forma para estudiar el entorno interno y externo de mercadeo.
          p En el siguiente video se pueden identificar elementos especficos de la matriz DOFA y la forma como se integran a travs de este recurso las debilidades, oportunidades, fortalezas y amenazas de una organizacin. 
        .col-lg-5.mb-4.mb-lg-0
          figure
            img.img-t(src='@/assets/curso/tema1/img90.svg').mx-auto
        .col-lg-8
          figure.p-5
            .video
              iframe(width="560" height="315" src="https://www.youtube.com/embed/84fuGpQeYg0" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen)

        .col-lg-10
          p Cuando se habla del <b>anlisis del entorno externo (oportunidades y amenazas)</b>, se refiere al macro entorno, donde se analiza el Pestel, es decir las variables: poltica, econmica, socio cultural, tecnolgica, ecolgica y legal, las cuales deben ser estudiadas desde el contexto empresarial. <b>El anlisis del entorno interno (fortalezas y debilidades)</b>, se refiere al micro entorno, donde se evalan las fortalezas y debilidades de una marca. Las empresas pueden evaluar sus propias fortalezas y debilidades a travs de una lista de verificacin donde se analice, principalmente, la filosofa empresarial (misin, visin y valores), la diferencia competitiva, los proveedores, los distribuidores, los clientes, los socios (si los hay) y la competencia. 
      .row.mb-5.justify-content-center.align-items-center(data-aos="fade-right")
        .col-lg-6
          h4 Paso 3. Formulacin de metas.  
          p Se refiere a los objetivos especficos que en la mayora de las unidades de negocio se encuentran la rentabilidad, el aumento en las ventas, el incremento en la participacin de mercado, el bloqueo de riesgos, la innovacin y la reputacin. La unidad de negocio determina estas metas para luego implementar un proceso de Administracin por Objetivos (APO); para que un sistema de APO marche, las metas de la unidad de negocio tienen que cumplir los siguientes criterios.
          .row.align-items-center
            .col-lg-5.bg6.h-100.p-2.me-3.mb-3
              figure
                img(src='@/assets/curso/tema1/img91.svg', style="width: 35px").mb-3
                p.mb-0 Deben adaptarse por jerarqua de manera descendente, de acuerdo con su importancia. 
            .col-lg-5.bg6.h-100.p-2.me-2.mb-3
              figure.pb-3
                img(src='@/assets/curso/tema1/img97.svg', style="width: 35px").mb-3
                p.mb-0 Las metas deben ser cuantitativas siempre que sea posible. 
            .col-lg-5.bg6.h-100.p-3.me-3.mb-3
              figure
                img(src='@/assets/curso/tema1/img95.svg', style="width: 35px").mb-3
                p.mb-0 Las metas deben ser realistas.
            .col-lg-5.bg6.h-100.p-2.me-3.mb-3
              figure.pb-3
                img(src='@/assets/curso/tema1/img96.svg', style="width: 35px").mb-3
                p.mb-0 Las metas deben ser consistentes.
        .col-lg-4.mb-4.mb-lg-0
            figure
              img.img-t(src='@/assets/curso/tema1/img92.png').m-auto
      .row.mb-5.justify-content-center.align-items-center(data-aos="fade-right")
        .col-lg-6
          h4 Paso 4. Formulacin estratgica. 
          p.mb-5 Las metas muestran lo que quiere conseguir una unidad de negocio; la estrategia es el plan para alcanzar el cumplimiento. Para lograr las metas cada negocio debe crear una estrategia de mercadeo, una estrategia tecnolgica (digital) y una estrategia de aprovisionamiento.
          h4 Paso 5. Formulacin e implementacin de programas. 
          p Se refiere a programas de mercadeo que permitan que la unidad pueda lograr liderazgo tecnolgico, reforzar el departamento I + D (investigacin y desarrollo), recopilar inteligencia tecnolgica, desarrollar productos innovadores, capacitar la fuerza de ventas o comunicar su liderazgo tecnolgico. La implementacin de los programas depender del contexto empresarial y de sus recursos. Una vez formulados los programas de mercadeo, los especialistas deben calcular los costos, para no pasarse del presupuesto empresarial
          
        .col-lg-4.mb-4.mb-lg-0
            figure
              img.img-t(src='@/assets/curso/tema1/img93.png').m-auto
      .row.mb-5.justify-content-center.align-items-center(data-aos="fade-right")
        .col-lg-4.mb-4.mb-lg-0
            figure
              img.img-t(src='@/assets/curso/tema1/img94.png').m-auto
        .col-lg-6
          h4 Paso 6. Implementacin. 
          p La estrategia es apenas uno de los 7 tems que participan en la prctica exitosa de las empresas. <b>Los 3 tems iniciales -estrategia, estructura y sistemas- son el <em>hardware</em> del xito; los otros 4 -estilo, habilidades, personal y valores compartidos- son el <em>software</em></b>.
          p.mb-5 El primer tem del software, estilo, representa que los empleados comparten una forma de comportarse y pensar. El segundo tem, habilidades, involucra que los empleados tienen habilidades que sirven para llevar a cabo la estrategia empresarial. El tem personal describe que la marca ha contratado personas capaces, asignndolas a los trabajos precisos para cada cual. El cuarto tem, valores compartidos, muestra que los empleados comparten los mismos valores y se guan por ellos. Cuando todos estos tems estn presentes, las empresas pueden tener ms xito a la hora de implementar su estrategia.
          h4 Paso 7. Control y retroalimentacin. 
          p Es hora de realizar un anlisis sobre lo realizado hasta el momento y de corregir lo que se deba corregir, incluso, presentando planes de mejoramiento.
    Separador
    #t_1_3
    .titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 1.3 Segmentacin 
    .row.mb-5.justify-content-center.align-items-center(data-aos="fade-right")
      .col-lg-4.mb-4.mb-lg-0
        figure
          img(src='@/assets/curso/tema1/img99.png').m-auto
      .col-lg-8(data-aos="fade-left")
        p Tal como afirman Rodrguez, Montes y Lpez (2007), en los mercados amplios, grandes y diversos, es una utopa que las marcas se puedan contactar con todos los clientes. Lo que s se puede hacer es dividir esos mercados en grupos de consumidores homogneos, o sea, segmentos que se conforman por tener las mismas necesidades y deseos. La marca, entonces, necesita elegir los segmentos de mercado que puede atender con eficacia.
        p Para ser ms competitivos, muchas marcas han adoptado en la actualidad el <em>marketing</em> dirigido, o sea, enfocarse en aquellas clientes que tienen mejor opcin de satisfacer. Para ser efectivo, el <em>marketing</em> dirigido exige que los especialistas en mercadeo:     
    .row.mb-5
      .col-12.col-lg-4.mb-3.h-100(data-aos="zoom-in")
        .row.bg9
          .col-3.px-0.align-items-center
            figure
              img.transf(src='@/assets/curso/tema1/img100.svg', alt='Texto que describa la imagen')
          .col-9.d-flex.align-items-center
            div
              p.mb-0 <b>Identifiquen y perfilen diferentes grupos de compradores, cuyas necesidades y deseos son distintas (segmentacin del mercado)</b>.
      .col-12.col-lg-4.mb-3.h-100(data-aos="zoom-in")
        .row.bg9
          .col-3.px-0.align-items-center
            figure
              img.transf(src='@/assets/curso/tema1/img101.svg', alt='Texto que describa la imagen')
          .col-9.d-flex.align-items-center
            div
              p.mb-0 <b> Elijan uno o ms segmentos de mercado donde pueda entrar (segmentos meta u objetivo)</b>. 
      .col-12.col-lg-4.mb-3.h-100(data-aos="zoom-in")
        .row.bg9
          .col-3.px-0.align-items-center
            figure
              img.transf(src='@/assets/curso/tema1/img102.svg', alt='Texto que describa la imagen')
          .col-9.d-flex.align-items-center
            div
              p.mb-0 <b> Instauren y notifiquen los beneficios diferenciales de la oferta de la marca para cada segmento objetivo (posicionamiento de mercado)</b>.   
    .row.mb-3(data-aos="fade-right")
      .col-auto.bg4.p-0
        .row.align-items-center
          .col-auto.pe-0
            figure
              img(src='@/assets/curso/tema1/img25.svg') 
          .col-8.col-lg-auto.me-3
            h3.mb-0 Bases para segmentar los mercados de consumo
  
    .row.mb-5.justify-content-center.align-items-center(data-aos="fade-right")
      .col-lg-3.mb-4.mb-lg-0
        figure
          img(src='@/assets/curso/tema1/img103.svg').m-auto
      .col-lg-8(data-aos="fade-left")
        p Segmentar es dividir el mercado en partes homogneas de acuerdo con los gustos y necesidades de las personas. La labor del especialista en mercadeo radica en identificar el nmero y naturaleza de los distintos segmentos que pueden conformar el mercado, y en elegir a cules se va a dirigir la marca. Los investigadores pretenden definir los segmentos a travs del anlisis de sus caractersticas descriptivas: las variables  geogrfica, demogrfica, psicogrfica y conductual. Luego exploran si los clientes de estos segmentos presentan distintas necesidades o respuestas a los productos. Independientemente de qu clase de estilo de segmentacin se use, la clave ser ajustar el programa de mercadeo para tener en cuenta las diferencias entre los clientes. 
    p.mb-5 La siguiente lnea de tiempo tiene como fin ampliar y detallar los conceptos propios de la segmentacin y cada una de sus lneas a nivel geogrfico, demogrfico, psicogrfico y conductual. 

    .tarjeta.tarjeta--gris.p-4.mb-5.bg10
      LineaTiempoC.color-acento-contenido(text-small)(data-aos="fade-right")
        .row.justify-content-lg-center.align-items-center(titulo="")
          .col-md-3.col-12.mb-4.mb-md-3
            figure
              img.img-t.mx-auto(src='@/assets/curso/tema1/img107.svg', alt='Texto que describa la imagen')
          .col-md-7.col-12.mb-2.mb-md-0
            h4 Segmentacin geogrfica
            p Permite concebir dnde estn localizados de forma geogrfica los consumidores potenciales del producto. Trabaja sobre el principio de que la gente que corresponde a esa zona geogrfica tiene necesidades, deseos y caractersticas culturales parecidas. Con estos datos las marcas pueden crear sus campaas de manera estratgica, usando el lenguaje correcto y comprendiendo si su oferta es importante para las personas de esa regin. Para hacer una segmentacin geogrfica debes tener en cuenta algunas variables:
            .row.justify-content-center.mb-5
              .col-md-6.col-lg.mb-2.mb-lg-0
                .tarjeta-avatar.patex
                  img.imgtem(src='@/assets/curso/tema1/img108.svg', style="width: 50px")
                  .tarjeta.color-acento-contenido2
                    .p-3
                      p <b>Localizacin</b>. Puede ir desde un continente hasta un vecindario. 
              .col-md-6.col-lg.mb-2.mb-lg-0
                .tarjeta-avatar.patex
                  img.imgtem(src='@/assets/curso/tema1/img109.svg', style="width: 50px")
                  .tarjeta.color-acento-contenido2
                    .p-3
                      p <b>Clima.</b> Relevancia de los cambios climticos en la conducta,  incluyendo estaciones.  
              .col-md-6.col-lg.mb-5.mb-lg-0
                .tarjeta-avatar.patex
                  img.imgtem(src='@/assets/curso/tema1/img110.svg', style="width: 50px")
                  .tarjeta.color-acento-contenido2
                    .p-3
                      p <b>Cultura.</b> Fundamental para crear campaas exitosas en cualquier parte del mundo. 
        .row.justify-content-lg-center.align-items-center(titulo="")
          .col-md-3.col-12.mb-4.mb-md-3
            figure
              img.img-t.mx-auto(src='@/assets/curso/tema1/img104.svg', alt='Texto que describa la imagen')
